@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  display: flex;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
  margin: 0;
  background-color: #f0f0f0;
  overflow-x: hidden;
  flex: 1;
}

html::-webkit-scrollbar {
  -webkit-appearance: none;
}

html::-webkit-scrollbar:vertical {
  width: 8px;
}

html::-webkit-scrollbar-thumb {
  background-color: #c89300;
  border-radius: 2rem;
}

main {
  margin-top: 15rem;
  height: 100%;
}

@media (min-width: 768px) {
  main {
    margin-top: 8rem;
  }
}
a {
  text-decoration: none;
  color: #FFFFFF;
}

p {
  font-size: 1.8rem;
  margin: 0.5rem;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  text-align: center;
  margin: 0;
  margin-bottom: 2rem;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
  font-weight: 700;
  text-align: start;
  margin: 0;
}

p {
  margin: 0;
}

.mensaje {
  position: fixed;
  display: flex;
  gap: 2rem;
  align-items: center;
  background-color: #FFFFFF;
  border: 2px solid #000000;
  border-radius: 2rem;
  padding: 2rem;
  bottom: 2rem;
  opacity: 0;
  animation-name: aparecer;
  animation-duration: 5s;
}
.mensaje img {
  width: 3.5rem;
}

.contenedor2 {
  padding: 4rem;
}

.alerta {
  text-align: center;
  font-size: 1.8rem;
}

.error {
  color: red;
}

.exito {
  color: green;
}

@keyframes aparecer {
  0% {
    right: -50rem;
    opacity: 1;
  }
  20% {
    right: 2rem;
    opacity: 1;
  }
  60% {
    right: 2rem;
    opacity: 1;
  }
  100% {
    right: -50rem;
  }
}
/** Media Queries **/
.contenedor-grid2, .contenedor-grid4 {
  display: grid;
  align-items: center;
  height: 100%;
}

.contenedor {
  margin: 2rem 10%;
}

.contenedor-grid2 {
  grid-template-columns: repeat(2, 1fr);
}

.contenedor-grid4 {
  grid-template-columns: repeat(4, 1fr);
}

.contenedor-100 {
  margin: 0;
  padding: 3rem 5%;
}

.contenedor-blanco {
  background-color: #FFFFFF;
  padding: 2rem;
  box-shadow: 0 0 0 2px rgb(255, 255, 255), 0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
}

.flexWrap {
  display: flex;
  flex-wrap: nowrap;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.botones {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-evenly;
}

.flex {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.boton {
  font-size: 1.8rem;
  padding: 1rem;
  border-radius: 1rem;
  border: none;
}

.btn-enviar {
  background-color: #ffc82f;
  font-weight: 700;
  margin-top: 2rem;
  transition: all ease-out 0.2s;
}

.btn-enviar:hover {
  background-color: #fbb900;
}

.negro {
  background-color: #000000;
  color: #FFFFFF;
  height: 100%;
}

.contenedor-texto {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  padding: 5rem 0;
}

.texto-color-gris {
  color: #D4D6DA;
  transition: all linear 0.1s;
}

.texto-color-gris:hover {
  color: #FFFFFF;
}

.formulario-login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2rem 5%;
  padding: 2rem 5rem;
}
.formulario-login label {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
.formulario-login input:not(:last-child) {
  width: 80%;
  font-size: 2rem;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid #000000;
  margin-bottom: 2rem;
  height: 3rem;
}

.contenedor-formulario {
  background-color: #FFFFFF;
  margin: 5rem 5rem;
  border-radius: 2rem;
}
.contenedor-formulario h3 {
  margin-top: 4rem;
}

.boton-enviar {
  border-radius: 1rem;
  width: 15rem;
  padding: 0.8rem;
  font-size: 2rem;
  border: none;
  color: #FFFFFF;
  background-color: #11234d;
  transition: all ease 0.2s;
}

.boton-enviar:hover {
  background-color: #1a3677;
}

@media (max-width: 768px) {
  .mitad {
    display: flex;
    flex-direction: column;
  }
  .contenedor-texto {
    width: 100%;
    border-radius: 0;
    padding: 2rem;
    margin-bottom: 1rem;
  }
  .contenedor-formulario {
    width: 100%;
    background-color: transparent;
  }
  .formulario-login {
    padding: 0;
  }
  #formulario {
    height: auto;
  }
}
.filter-bar {
  display: flex;
  gap: 3rem;
  padding: 10px;
  margin: 0 20% 2rem 20%;
}
.filter-bar label {
  font-size: 2rem;
  font-weight: 700;
}
.filter-bar select {
  font-size: 1.8rem;
  height: 2.2rem;
  border: none;
  border-bottom: 3px solid #000000;
}

@media (min-width: 768px) {
  .filter-bar {
    margin: 0;
  }
}
/* Estilos para mostrar los productos */
.product-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 20px;
}

.product-item {
  border: 1px solid #ccc;
  padding: 10px;
}

.filtro {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.contenedor {
  padding: 2rem;
}

.contenedor-admin {
  display: flex;
  flex-direction: column;
  margin: 2rem 10%;
}

.admin {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.admin h3 {
  margin: 1rem;
}
.admin a {
  background-color: #11234d;
  color: #FFFFFF;
  border: 1px solid transparent;
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
  border-radius: 1rem;
  transition: all linear 0.2s;
}
.admin a:hover {
  background-color: transparent;
  border: 1px solid #11234d;
  color: #000000;
}
.admin .no-fondo {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
}
.admin .no-fondo img {
  width: 2rem;
}
.admin .no-fondo:hover {
  border: none;
}

.accion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.accion h3 {
  font-weight: 400;
}

.contenedor-tabla {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 100%;
}

.barra-busqueda {
  width: 90%;
  margin: 1rem;
  font-size: 1.8rem;
}

table {
  font-size: 1.5rem;
  border-style: solid;
  padding: 1rem;
  width: 100%;
}
table tr {
  height: auto;
}
table td {
  height: 100%;
}
table td a {
  display: flex;
  justify-content: center;
  align-items: center;
}
table td img {
  width: 8rem;
}
table .no {
  height: 4rem;
  border-bottom: 1px solid #000000;
}

.contenedor-admin {
  grid-template-columns: 60% auto;
}

.usuario span {
  font-weight: 400;
  color: #2349a1;
}

.contenedor-opciones {
  display: flex;
  gap: 1rem;
  border-radius: 0.5rem;
  background-color: #D4D6DA;
  padding: 1rem;
  margin-bottom: 1.2rem;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 1px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.contenedor-opciones img {
  width: 100%;
  max-width: 10rem;
}

.img-opcion {
  display: flex;
  justify-content: center;
  align-items: center;
}

.opciones {
  display: flex;
  flex-direction: column;
}

.opcion {
  color: #000000;
  transition: all ease-in-out 0.2s 0.1s;
}
.opcion p {
  font-size: 1.5rem;
}

@media (min-width: 1024px) {
  .opciones {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.contenedor-opciones:hover {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.contenedor-comprar .vista-comprar {
  border-bottom: 1px solid #000000;
  padding: 2rem 10%;
  background-color: #D4D6DA;
}
.contenedor-comprar .vista-comprar h2, .contenedor-comprar .vista-comprar p {
  margin: 0;
}
.contenedor-comprar .vista-comprar .grid2 {
  display: grid;
  grid-template-columns: 30% auto;
  align-items: center;
}
.contenedor-comprar .vista-comprar .grid2 p {
  font-weight: 700;
}
.contenedor-comprar .vista-comprar .grid2 span {
  text-transform: uppercase;
  color: #11234d;
}
.contenedor-comprar .contenido-comprar {
  margin: 3rem 10%;
