  grid-template-columns: 60% auto;
}
.contenedor-comprar .contenedor-seccion-comprar {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem 4rem;
}
.contenedor-comprar .contenedor-seccion-comprar .divForm {
  padding: 2rem 0;
}
.contenedor-comprar .contenedor-seccion-comprar .formulario {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.contenedor-comprar .contenedor-seccion-comprar .formulario .input {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.contenedor-comprar .contenedor-seccion-comprar .formulario .input label, .contenedor-comprar .contenedor-seccion-comprar .formulario .input input, .contenedor-comprar .contenedor-seccion-comprar .formulario .input select {
  font-size: 1.6rem;
}
.contenedor-comprar .contenedor-seccion-comprar .formulario .input label {
  font-weight: 700;
}
.contenedor-comprar .contenedor-seccion-comprar .formulario .input input, .contenedor-comprar .contenedor-seccion-comprar .formulario .input select {
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: 1px solid #D4D6DA;
}
.contenedor-comprar .contenedor-seccion-comprar .formulario .dosInput {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}
.contenedor-comprar .contenedor-seccion-comprar .formulario .dosInput div {
  display: flex;
  flex-direction: column;
  width: 50%;
}
.contenedor-comprar .contenedor-seccion-comprar .formulario .boton-enviar {
  font-size: 1.6rem;
  padding: 0.8rem;
  width: min-content;
  border-radius: 0.5rem;
}

.contenedorSombra {
  box-shadow: 0 0 10px 6px #D4D6DA;
}

.Info-comprar p {
  font-size: 2rem;
}
.Info-comprar span {
  font-weight: 700;
}
.Info-comprar .total {
  margin-top: 2rem;
  font-size: 2.4rem;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  background-color: #11234d;
  color: #FFFFFF;
  position: fixed;
  height: 15rem;
  z-index: 4;
  top: 0;
}
.header a {
  display: flex;
  justify-content: center;
  width: 100%;
}
.header a .img {
  width: 100%;
}

@media (min-width: 768px) {
  .header {
    height: 8rem;
    flex-direction: row;
  }
  .header a {
    width: auto;
  }
}
.busqueda {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60%;
}
.busqueda input {
  height: 3rem;
  font-size: 2rem;
  width: 100%;
}

#search-results {
  position: absolute;
}

.header-img {
  gap: 1rem;
}
.header-img img {
  width: 3rem;
  filter: invert(1);
  transition: all linear 0.2s;
}
.header-img img:hover {
  filter: invert(0.7);
}

.logo_img {
  width: 100%;
  max-width: 20rem;
  padding: 1rem 0;
}

.contenedor-logo {
  width: 20%;
}

.busqueda-iconos {
  width: auto;
  min-width: 60%;
  margin: 1rem 0 2rem 0;
  justify-content: space-around;
}
.busqueda-iconos .busqueda {
  margin: 0;
}

@media (max-width: 1024px) {
  .busqueda-iconos {
    justify-content: center;
    width: 90%;
  }
  .busqueda-iconos .busqueda {
    margin-right: 2rem;
  }
}
.gris {
  background-color: #FFFFFF;
  border-bottom: 2px solid #000000;
}

.categorias {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 2fr));
  margin: 1rem;
  gap: 2rem;
}

.categoria {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #000000;
}
.categoria p {
  font-size: 1.8rem;
}
.categoria .categoria-img img {
  width: 6rem;
  height: 6rem;
  background-color: #D4D6DA;
  border-radius: 50%;
  transition: all ease-in 0.2s;
}

.categoria-img:hover img {
  transform: scale(1.1);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.contenedor30-70 {
  display: flex;
  flex-direction: column;
}

.barra-izquierda {
  border: none;
  border-bottom: 2px solid #000000;
  width: 100%;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.barra-izquierda h3 {
  font-weight: 400;
}

@media (max-width: 768px) {
  .barra-izquierda {
    height: auto;
  }
}
.catalogo {
  height: 100%;
  max-width: 100vw;
  padding: 4rem;
}
.catalogo span {
  color: #2349a1;
}
.catalogo h2 {
  margin-top: 0;
}

.productos {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  gap: 1.5rem;
}
.productos h2 {
  margin-bottom: 2rem;
}

.producto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex: 1 20rem;
  border: 1px solid #000000;
  border-radius: 0.6rem;
  padding: 1rem;
  background-color: #FFFFFF;
  transition: all linear 0.2s 0.2s;
}

.productosInicio .producto:hover {
  transform: scale(1.05);
}

.productoContenedor {
  color: #000000;
}
.productoContenedor img {
  width: 80%;
}
.productoContenedor .precio {
  font-weight: 700;
  color: #c89300;
}
.productoContenedor .nomProducto {
  border-top: 1px solid #000000;
  padding: 1rem;
  font-size: 1.6rem;
  font-weight: 700;
}
.productoContenedor .descProducto {
  font-size: 1.2rem;
  margin: 1rem 0;
}
.productoContenedor .invProducto {
  margin-top: 1rem;
  font-size: 1.4rem;
}

.contenedor-producto {
  margin: 5rem 0;
  border: 1px solid #000000;
  border-radius: 1rem;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
}
.contenedor-producto img {
  border-radius: 1rem;
  border: none;
  padding: 3rem;
}
.contenedor-producto .imagen-producto {
  width: 100%;
  display: flex;
  justify-content: center;
}
.contenedor-producto .imagen-producto img {
  width: 100%;
  max-width: 60rem;
}
.contenedor-producto .informacion-producto {
