  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: start;
}
.contenedor-producto .informacion-producto p {
  font-size: 1.8rem;
  margin: 0;
  text-justify: 1.5;
  margin-bottom: 1rem;
}
.contenedor-producto .informacion-producto span {
  font-size: 2.2rem;
  font-weight: 700;
  display: block;
}
.contenedor-producto .informacion-producto h2 {
  text-align: start;
  margin: 0;
  margin-bottom: 5rem;
}

.formulario-producto {
  margin-top: 3rem;
}
.formulario-producto .formulario-compra {
  display: flex;
  flex-direction: column;
}
.formulario-producto .input-number {
  width: 12rem;
  font-size: 1.5rem;
  height: 3rem;
  margin-bottom: 1rem;
}
.formulario-producto label {
  font-size: 1.8rem;
}

.boton-comprar {
  background-color: #11234d;
  color: white;
  font-weight: 600;
  transition: all linear 0.2s;
}

.boton-carrito {
  background-color: #ffc82f;
  font-weight: 600;
  transition: all linear 0.2s;
}

.boton-carrito:hover {
  background-color: #fbb900;
}

.boton-comprar:hover {
  background-color: #1a3677;
}

@media (min-width: 1024px) {
  .contenedor-producto {
    flex-direction: row;
  }
  .contenedor-producto img {
    padding: none;
  }
  .imagen-producto {
    width: 40%;
    border-right: 1px solid #000000;
  }
  .informacion-producto {
    width: 60%;
  }
}
.formulario-admin {
  padding: 3rem;
  margin: 2rem 15%;
  border-radius: 1rem;
  background-color: #FFFFFF;
  border: 1px solid #000000;
}
.formulario-admin a {
  font-size: 1.5rem;
  font-weight: 400;
  color: #000000;
  margin-top: 0.5rem;
  transition: all ease 0.2s;
}
.formulario-admin a:hover {
  color: #11234d;
}
.formulario-admin .formulario {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
