.formulario-admin .formulario input, .formulario-admin .formulario textarea, .formulario-admin .formulario select {
  font-size: 1.8rem;
  width: 90%;
}
.formulario-admin .formulario input:not(:last-child), .formulario-admin .formulario textarea {
  margin-bottom: 1.5rem;
}
.formulario-admin .formulario .boton-enviar {
  margin-top: 2rem;
