}

@media (max-width: 480px) {
  .formulario-admin {
    margin: 2rem 5%;
  }
}
.contenedor-carrito {
  display: grid;
  grid-template-columns: 65% auto;
  margin-top: 5rem;
  gap: 5rem;
}
.contenedor-carrito .flex {
  justify-content: start;
  text-align: start;
}
.contenedor-carrito .flex h3, .contenedor-carrito .flex p {
