}

.contenedor-carrito-producto {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #D4D6DA;
  border-top: 1px solid #D4D6DA;
  padding: 2rem 5rem;
  align-items: center;
}
.contenedor-carrito-producto .imagen {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
}
.contenedor-carrito-producto img {
  width: 100%;
  max-width: 15rem;
}
.contenedor-carrito-producto .texto {
  width: 50%;
}
.contenedor-carrito-producto .texto span {
  font-weight: 700;
}
.contenedor-carrito-producto form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.contenedor-carrito-producto form input {
  font-size: 1.8rem;
  border: 1px solid #D4D6DA;
  border-radius: 1rem;
  padding: 0.5rem;
  width: 10rem;
  margin-top: 1rem;
}
.contenedor-carrito-producto form div {
  display: flex;
